<template>
  <div>
        <!-- start page title -->
        <div class="row">
            <div class="col-12">
                <div class="page-title-box d-flex align-items-center justify-content-between">
                    <h4 class="mb-0 font-size-18">Balancete</h4>
                    <div class="page-title-right">
                        <ol class="breadcrumb m-0">
                            <li class="breadcrumb-item" @click="imprimir_balancete()">Imprimir</li>
                        </ol>                        
                    </div>

                </div>
            </div>
        </div>
        <!-- end page title -->
        <div class="row" style="padding:10px">
            <div class="col-sm-12 col-md-3">                  
                <label >Nível de Detalhamento  
                    <select @input="debounceBusca" v-model="filtro.nivel" class="custom-select custom-select-sm form-control form-control-sm">
                        <option :value="-1">Todos</option>
                        <option :value="0">0</option>
                        <option :value="1">1</option>
                        <option :value="2">2</option>
                        <option :value="3">3</option>
                        <option :value="4">4</option>
                        <option :value="5">5</option>
                        <option :value="6">6</option>
                        <option :value="7">7</option>
                        <option :value="8">8</option>
                        <option :value="9">9</option>
                    </select>
                </label>           
            </div>
       
            <div class="col-sm-12 col-md-3">
                <label style="width:90%">Contas Zeradas:
                    <select @input="debounceBusca" v-model="filtro.somente_com_lancamento" class="form-control form-control-sm">
                        <option :value="1">Não</option>
                        <option :value="0">Sim</option>                                                    
                    </select>
                </label>
            </div>
         <div class="col-sm-12 col-md-4">
                <label style="width:90%">Data Referência:
                     <date-picker  :clearable="false" v-model="filtro.data" :format="'DD/MM/YYYY'" :disabledDate="desabilitaDataFutura" @input="debounceBusca"></date-picker>
                </label>                  
            </div>
        </div>
        <div class="row">
            <div class="col-12">
                <div class="card">
                    <div class="card-body">                        
                       <div class="table-responsive">
                            <table class="table mb-0" >
                                <thead>
                                    <tr >
                                        <th> Conta </th>
                                        <th> Direitos (+) </th>
                                        <th> Obrigações (-) </th>
                                        <th> Saldo </th>
                                    </tr>
                                </thead>
                                <tbody>         
                                    <tr class="odd item-raiz">
                                        <th>
                                            Totais
                                        </th>
                                        <th>
                                           {{total_debito | currency($store.state.moeda) }}
                                        </th>
                                        <th>
                                           {{total_credito | currency($store.state.moeda) }}
                                        </th>
                                        <th>
                                           {{total_saldo | currency($store.state.moeda) }}
                                        </th>
                                    </tr>       
                                    <tr  class="odd" v-for="dado in ativo" :key="dado.codigo" :class="{'item-raiz': dado.nivel == 1 }">
                                        <td>
                                            <span v-for="(x, i) in dado.codigo.toString().split('.')" :key="i">&nbsp;&nbsp;&nbsp;</span>
                                                {{dado.codigo}} | {{dado.nome}}
                                        </td>
                                        <td>
                                           {{dado.debito | currency($store.state.moeda) }}
                                        </td>
                                        <td>
                                           {{dado.credito | currency($store.state.moeda) }}
                                        </td>
                                        <td>
                                           {{(dado.debito - dado.credito) | currency($store.state.moeda) }}
                                        </td>
                                    </tr>
                                    <tr  class="odd" v-for="dado in passivo" :key="dado.codigo" :class="{'item-raiz': dado.nivel == 1 }">
                                        <td>
                                            <span v-for="(x, i) in dado.codigo.toString().split('.')" :key="i">&nbsp;&nbsp;&nbsp;</span>
                                                {{dado.codigo}} | {{dado.nome}}
                                        </td>
                                        <td>
                                           {{dado.debito | currency($store.state.moeda) }}
                                        </td>
                                        <td>
                                           {{dado.credito | currency($store.state.moeda) }}
                                        </td>
                                        <td>
                                           {{(dado.debito - dado.credito) | currency($store.state.moeda) }}
                                        </td>
                                    </tr>
                                    <tr class="odd item-raiz">
                                        <th>
                                            Totais
                                        </th>
                                        <th>
                                           {{total_debito | currency($store.state.moeda) }}
                                        </th>
                                        <th>
                                           {{total_credito | currency($store.state.moeda) }}
                                        </th>
                                        <th>
                                           {{total_saldo | currency($store.state.moeda) }}
                                        </th>
                                    </tr>
                                </tbody>
                            </table>
                       </div>

                    </div>
                </div>
            </div> 
        </div> 

     
     


  </div>
</template>

<script>
import { CaixaService } from '../../../services'

import moment from 'moment'
const caixaService = new CaixaService();

export default {
     data(){
        return{
            ativo: [],
            passivo: [],
            timer:null,
            moeda_padrao: null,
            filtro:{
                data: new Date(),
                nivel: 4,
                somente_com_lancamento: 0,
            },
            debounce: null,            
        }   
    },
    
  
    methods: {
        
     debounceBusca: function(){
            clearTimeout(this.timer);
            this.timer = setTimeout(() => {
                this.buscarBalancete();
            }, 800);
        },
        
        buscarBalancete:function(){
            let f = {...this.filtro};
            f.data = moment(f.data).format('DD-MM-YYYY');
            caixaService.balancete(f).then(
                response => {
                    if(!this.moeda_padrao)
                        this.moeda_padrao = response.data.moeda_padrao;
                    this.ativo = Object.values(response.data.ativo).map(x => {
                        x.nivel = x.codigo.split('.').length;
                        return x;
                    });
                    this.passivo = Object.values(response.data.passivo).map(x => {
                        x.nivel = x.codigo.split('.').length;
                        return x;
                    });
                }
            )
        },
        desabilitaDataFutura: function(data) {
            let hoje = new Date();
            return data > hoje
        },

        imprimir_balancete: function(){
             let f = {...this.filtro};
            f.data = moment(f.data).format('DD-MM-YYYY');

            let url = caixaService.link_balancete(f);
            window.open(url,'_blank');
        }
    },
    computed:{
        total_debito(){
            if(this.ativo.length == 0)
                return 0
            return (this.ativo[0].debito - this.ativo[0].credito);
        },
        total_credito(){
            if(this.passivo.length == 0)
                return 0
            return (this.passivo[0].debito - this.passivo[0].credito);
        },
        total_saldo(){
            return this.total_debito + this.total_credito;
        },

       
    },
    mounted(){
        this.buscarBalancete();
    },
};
</script>
